import { Link, useMatch } from "react-router-dom";
import { motion } from "framer-motion";

export default function Header() {
  const homeMatch = useMatch("/");
  const acordianMatch = useMatch("/acordian");
  const appStoreMatch = useMatch("/app-store");

  return (
    <header className="w-full p-5 px-10 bg-slate-800 flex items-center text-slate-200 text-2xl font-semibold  gap-5">
      <div className="relative">
        <Link to={"/"}></Link>
        {homeMatch && (
          <motion.div
            layoutId="underline"
            className="absolute -bottom-1 left-0 w-full h-1 bg-slate-500 rounded-full"
          ></motion.div>
        )}
      </div>
      <hr />
      <div className="relative">
        <Link to={"/acordian"}></Link>
        {acordianMatch && (
          <motion.div
            layoutId="underline"
            className="absolute -bottom-1 left-0 w-full h-1 bg-slate-500 rounded-full"
          ></motion.div>
        )}
      </div>
      <div className="relative">
        <Link to={"/app-store"}> </Link>
        {appStoreMatch && (
          <motion.div
            layoutId="underline"
            className="absolute -bottom-1 left-0 w-full h-1 bg-slate-500 rounded-full"
          ></motion.div>
        )}
      </div>
    </header>
  );
}
